import Vue from 'vue'
import Router from 'vue-router'
import CRD from '../src/components/Card.vue'
import Centered from '../src/components/Centered.vue'
import Skeleton from '../src/components/Skeleton.vue'
import Blades from '../src/components/Blades.vue'

Vue.use(Router)


export default new Router({
  //mode: 'history',
  routes: [
    { path: '/', component: CRD },
    { path: '/card', component: CRD },
    { path: '/centered', component: Centered },
    { path: '/skeleton', component: Skeleton },
    { path: '/blades', component: Blades }
  ],
  scrollBehavior() {
      return {x: 0, y: 0}
  }
})
